import React, {useEffect} from 'react';
import './Checkout.css'
import BodyTemplate from "../template/BodyTemplate";
import RouteTemplate from "../template/RouteTemplate";
import CheckoutLeft from "./Component/CheckoutLeft";
import CheckoutRight from "./Component/CheckoutRight";
import MetaTag from "../template/MetaTag/MetaTag";
import {APP_URL, DESC_DEFAULT, LOGO_DEFAULT} from "../../constants/Constants";

const Checkout = () => {
    return (
        <BodyTemplate>
            <MetaTag title={'Thanh toán'} description={DESC_DEFAULT} url={APP_URL} image_url={LOGO_DEFAULT}/>
            <RouteTemplate data={[
                {
                    name: 'Trang chủ',
                    path: '/'
                },
                {
                    name: "Giỏ hàng",
                    path: '/cart'
                },
                {
                    name: "Thanh toán"
                }
            ]}/>
            <section className='checkout-container'>
                <div className='checkout-right-container checkout-1'>
                    <CheckoutRight/>
                </div>
                <div className='checkout-left-container'>
                    <CheckoutLeft/>
                </div>
                <div className='checkout-right-container checkout-2'>
                    <CheckoutRight/>
                </div>
            </section>
        </BodyTemplate>
    )
        ;
};

export default Checkout;