import React, {useEffect, useRef, useState} from 'react';
import './CheckoutLeft.css'
import {getItems, postItems} from "../../../api/api";
import LinkTemplate from "../../template/LinkTemplate";
import {useDispatch, useSelector} from "react-redux";
import {openCheckoutDialog} from "../../../redux/actions/actions";
import {useHistory} from 'react-router-dom'
import {ValidateInput} from "../../../helper/validate/ValidateInput";

const CheckoutLeft = () => {
    const [province, setProvince] = useState([])
    const [district, setDistrict] = useState([])
    const [isSending, setSending] = useState(false)
    const [ward, setWard] = useState([])
    const dispatch = useDispatch()
    const [state, setState] = useState({
        name: '',
        phone_number_1: '',
        phone_number_2: '',
        city: 1,
        district: 1,
        ward: 1,
        address: '',
        vat_required: 0,
        tax_number: '',
        company_name: '',
        company_address: '',
        payment_method: 0,
        ship_method: 0,
        time_ship: 0,
    })
    const [error, setError] = useState({
        name: {
            isError: false,
            message: ''
        },
        phone: {
            isError: false,
            message: ''
        },
        address: {
            isError: false,
            message: ''
        },
        tax_number: {
            isError: false,
            message: ''
        },
        company_name: {
            isError: false,
            message: ''
        },
        company_address: {
            isError: false,
            message: ''
        },
    })
    const history = useHistory()
    useEffect(() => {
        getItems('/location/city', {timeout: 20000})
            .then(res => {
                setProvince(res.data.list)
            })
            .catch(error => {
                console.log(error)
            })
    }, [])
    useEffect(() => {
        if (state.city === 0) return;
        setWard([])
        getItems(`/location/city/${state.city}/district`, {timeout: 20000})
            .then(res => {
                setDistrict(res.data.list)
            })
            .catch(error => {
                console.log(error)
            })
    }, [state.city])
    useEffect(() => {
        if (state.district === 0) return;
        getItems(`/location/district/${state.district}/ward`, {timeout: 20000})
            .then(res => {
                setWard(res.data.list)
            })
            .catch(error => {
                console.log(error)
            })
    }, [state.district])
    const handleChange = (e) => {
        let name = e.target.name
        let val = e.target.value
        setState({
            ...state,
            [name]: val
        })
    }
    const handleCheckBox = (e) => {
        const name = e.target.name
        let val = e.target.checked ? 1 : 0
        setState({
            ...state,
            [name]: val
        })
    }
    const cartData = useSelector(state => state.cartReducer)
    const handleOrder = () => {
        if (cartData.length === 0) {
            alert("Không có sản phẩm nào trong giỏ hàng. Trở về xem thêm sản phẩm")
            history.push('/')
            return;
        }
        if (state.name === '') {
            setError({
                ...error,
                name: {
                    isError: true,
                    message: "You need to fill in this field"
                }
            })
            window.scrollTo(0, 0)
            return
        } else if (state.phone_number_1 === '' && state.phone_number_2 === '') {
            setError({
                ...error,
                phone: {
                    isError: true,
                    message: "You need to fill at least 1 phone number"
                }
            })
            window.scrollTo(0, 0)
            return
        }
        if (!ValidateInput(state.name)) {
            setError({
                ...error,
                name: {
                    isError: true,
                    message: "Chuỗi không được chứa kí tự đặc biệt!"
                }
            })
            window.scrollTo(0, 0)
            return
        }
        if (!ValidateInput(state.phone_number_1) || !ValidateInput(state.phone_number_2)) {
            setError({
                ...error,
                name: {
                    isError: true,
                    message: "Chuỗi không được chứa kí tự đặc biệt!"
                }
            })
            window.scrollTo(0, 0)
            return
        }
        if (!ValidateInput(state.address)) {
            setError({
                ...error,
                address: {
                    isError: true,
                    message: "Chuỗi không được chứa kí tự đặc biệt!"
                }
            })
            window.scrollTo(0, 0)
            return
        }
        if (!ValidateInput(state.tax_number)) {
            setError({
                ...error,
                tax_number: {
                    isError: true,
                    message: "Chuỗi không được chứa kí tự đặc biệt!"
                }
            })
            window.scrollTo(0, 0)
            return
        }
        if (!ValidateInput(state.company_name)) {
            setError({
                ...error,
                company_name: {
                    isError: true,
                    message: "Chuỗi không được chứa kí tự đặc biệt!"
                }
            })
            window.scrollTo(0, 0)
            return
        }
        if (!ValidateInput(state.company_address)) {
            setError({
                ...error,
                company_address: {
                    isError: true,
                    message: "Chuỗi không được chứa kí tự đặc biệt!"
                }
            })
            window.scrollTo(0, 0)
            return
        }
        const data = {
            name: state.name,
            phone_number_1: state.phone_number_1,
            phone_number_2: state.phone_number_2,
            city_id: state.city,
            district_id: state.district,
            ward_id: state.ward,
            address: state.address,
            vat_required: state.vat_required,
            tax_number: state.tax_number,
            company_name: state.company_name,
            company_address: state.company_address,
            payment_method: state.payment_method,
            ship_method: state.ship_method,
            time_ship: state.time_ship,
            list_orders: cartData.map(item => {
                return {
                    product_id: item.product_id,
                    quantity: item.amount
                }
            })
        }
        setSending(true)
        postItems('/order', data, {timeout: 20000})
            .then(res => {
                console.log(res)
                setSending(false)
                dispatch(openCheckoutDialog({
                    order_id: res.data.order_id
                }))
            })
            .catch(error => {
                setSending(false)
                alert("Error when sending request! Please try again")
            })
    }
    return (
        <section>
            <p className='checkout-left-title-info'><i className="fas fa-map-marker-alt checkout-left-icon"></i>
                Thông tin mua hàng</p>
            <div className='checkout-left-body-info'>
                <div className='checkout-left-input-container input-mobile-available'>
                    <p className='checkout-left-title'>Họ và tên (*)</p>
                    <div className='checkout-left-input-wrapper'>
                        <input className='checkout-left-input' placeholder={'Nhập họ và tên'} type='text'
                               name="name" value={state.name} onChange={handleChange}/>
                        {
                            error.name.isError ?
                                <p className='checkout-left-error'>{error.name.message}</p> : null
                        }
                    </div>
                </div>
                <div className='checkout-left-input-container input-mobile-available'>
                    <p className='checkout-left-title'>Số điện thoại (*)</p>
                    <div className='checkout-left-input-wrapper'>
                        <div className='checkout-left-phone-container'>
                            <input className='checkout-left-input inline-input' placeholder={'Số điện thoại'}
                                   type='number' name="phone_number_1" value={state.phone_number_1}
                                   onChange={handleChange}/>
                            <input className='checkout-left-input inline-input' placeholder={'Số khác (Tùy chọn)'}
                                   type='number' name="phone_number_2" value={state.phone_number_2}
                                   onChange={handleChange}/>
                        </div>
                        {
                            error.phone.isError ?
                                <p className='checkout-left-error'>{error.phone.message}</p> : null
                        }
                    </div>
                </div>
                <div className='checkout-left-input-container input-mobile-available'>
                    <p className='checkout-left-title'>Tỉnh/Tp</p>
                    <div className='checkout-left-input-wrapper'>
                        <select className='checkout-left-input select-input' name='city' onChange={handleChange}
                                value={state.city}>
                            <option value={1}>Chọn tỉnh/thành phố</option>
                            {
                                province.map((item, index) => {
                                    if (item.id === 1) return null
                                    return <option value={item.id} key={index}> {item.name}</option>
                                })
                            }
                        </select>
                    </div>
                </div>
                <div className='checkout-left-input-container input-mobile-available'>
                    <p className='checkout-left-title'>Quận/Huyện</p>
                    <div className='checkout-left-input-wrapper'>
                        <select className='checkout-left-input select-input' name='district' onChange={handleChange}
                                value={state.district}>
                            <option value={1}>Chọn quận/huyện</option>
                            {
                                district.map((item, index) => {
                                    if (item.id === 1) return null;
                                    return <option value={item.id} key={index}> {item.name}</option>
                                })
                            }
                        </select>
                    </div>
                </div>
                <div className='checkout-left-input-container input-mobile-available'>
                    <p className='checkout-left-title'>Phường/Xã</p>
                    <div className='checkout-left-input-wrapper'>
                        <select className='checkout-left-input select-input' name='ward' onChange={handleChange}
                                value={state.ward}>
                            <option value={1}>Chọn phường/xã</option>
                            {
                                ward.map((item, index) => {
                                    if (item.id === 1) return null
                                    return <option value={item.id} key={index}> {item.name}</option>
                                })
                            }
                        </select>
                    </div>
                </div>
                <div className='checkout-left-input-container input-mobile-available'>
                    <p className='checkout-left-title'>Địa chỉ</p>
                    <div className='checkout-left-input-wrapper'>
                    <textarea className='checkout-left-input text-area-input' type='text'
                              placeholder={'Nhập đầy đủ địa chỉ, số nhà, phường/xã,...'}
                              name="address" value={state.address} onChange={handleChange}/>
                        {
                            error.address.isError ?
                                <p className='checkout-left-error'>{error.address.message}</p> : null
                        }
                    </div>
                </div>

                <div className='checkout-left-input-container' style={{justifyContent: 'center'}}>
                    <input type={'checkbox'} className='checkout-left-input-checkbox' id='check-out-left-checkbox-1'
                           placeholder={'Nhập đầy đủ địa chỉ, số nhà, phường/xã,...'}
                           name="vat_required" value={state.vat_required} onChange={handleCheckBox}/>
                    <label htmlFor={'check-out-left-checkbox-1'} className='checkout-left-body-checkbox'>Yêu cầu
                        VIETNHAT xuất hóa đơn VAT cho công ty hoặc tổ
                        chức</label>
                </div>
                {
                    !state.vat_required ? null :
                        <div>
                            <div className='checkout-left-input-container input-mobile-available'>
                                <p className='checkout-left-title'>Mã số thuế</p>
                                <div className='checkout-left-input-wrapper'>
                                    <input className='checkout-left-input' placeholder={'Nhập mã số thuế'}
                                           name="tax_number" value={state.tax_number} onChange={handleChange}/>
                                    {
                                        error.tax_number.isError ?
                                            <p className='checkout-left-error'>{error.tax_number.message}</p> : null
                                    }
                                </div>
                            </div>
                            <div>
                                <div className='checkout-left-input-container input-mobile-available'>
                                    <p className='checkout-left-title'>Tên công ty</p>
                                    <div className='checkout-left-input-wrapper'>
                                        <input className='checkout-left-input'
                                               placeholder={'Nhập tên công ty trên hóa đơn'}
                                               name="company_name" value={state.company_name} onChange={handleChange}/>
                                        {
                                            error.company_name.isError ?
                                                <p className='checkout-left-error'>{error.company_name.message}</p> : null
                                        }
                                    </div>
                                </div>
                            </div>
                            <div>
                                <div className='checkout-left-input-container input-mobile-available'>
                                    <p className='checkout-left-title'>Địa chỉ</p>
                                    <div className='checkout-left-input-wrapper'>
                                        <input className='checkout-left-input' placeholder={'Nhập địa chỉ công ty'}
                                               name="company_address" value={state.company_address}
                                               onChange={handleChange}/>
                                        {
                                            error.company_address.isError ?
                                                <p className='checkout-left-error'>{error.company_address.message}</p> : null
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                }
            </div>
            <p className='checkout-left-title-info' style={{marginTop: '1em'}}>
                <i className="far fa-credit-card" style={{
                    marginRight: '0.5em',
                    color: 'var(--second-bg-color)',
                    fontSize: '1.2em'
                }}></i>
                Chọn hình thức thanh toán</p>
            <div className='checkout-left-body-info' style={{padding: '2em', paddingBottom: '1em'}}>
                <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                    <input type={'radio'} className='checkout-left-input-checkbox' id='payment-method-1'
                           name="payment_method" value={0} onChange={handleChange}
                           checked={Number(state.payment_method) === 0}/>
                    <label htmlFor={'payment-method-1'} className='checkout-left-body-checkbox'>Giao hàng và thu tiền
                        tại nhà</label>
                </div>
                <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                    <input type={'radio'} className='checkout-left-input-checkbox' id={'payment-method-2'}
                           name="payment_method" value={1} onChange={handleChange}
                           checked={Number(state.payment_method) === 1}/>
                    <label htmlFor={'payment-method-2'} className='checkout-left-body-checkbox'>Nhận hàng và thanh toán
                        trực tiếp tại cửa hàng</label>
                </div>
            </div>
            <p className='checkout-left-title-info' style={{marginTop: '1em'}}>
                <i className="fa fa-truck" style={{
                    marginRight: '0.5em',
                    color: 'var(--second-bg-color)',
                    fontSize: '1.2em'
                }}></i>
                Chọn hình thức vận chuyển</p>
            <div className='checkout-left-body-info' style={{padding: '2em', paddingTop: '1em', paddingBottom: '1em'}}>
                <p>Cách thức vận chuyển:</p>
                <div style={{paddingLeft: '1em'}}>
                    <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                        <input type={'radio'} className='checkout-left-input-checkbox' id='ship-method-1'
                               name="ship_method" value={0} onChange={handleChange}
                               checked={Number(state.ship_method) === 0}/>
                        <div>
                            <label htmlFor={'ship-method-1'} className='checkout-left-body-checkbox'>Vận chuyển miễn
                                phí</label>
                            <div style={{fontWeight: 'bold', paddingLeft: '0.5em', paddingTop: '0.5em'}}>Nội thành Hà
                                Nội
                                dưới 10km
                            </div>
                        </div>
                    </div>
                    <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                        <input type={'radio'} className='checkout-left-input-checkbox' id='ship-method-2'
                               name="ship_method" value={1} onChange={handleChange}
                               checked={Number(state.ship_method) === 1}/>
                        <div>
                            <label htmlFor={'ship-method-2'} className='checkout-left-body-checkbox'>Vận chuyển có
                                phí</label>
                            <div style={{fontWeight: 'bold', paddingLeft: '0.5em', paddingTop: '0.5em'}}>Từ Km thứ 10
                                trở đi
                                vận chuyển 13.000 đồng/km
                            </div>
                        </div>
                    </div>
                    <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                        <input type={'radio'} className='checkout-left-input-checkbox' id='ship-method-3'
                               name="ship_method" value={2} onChange={handleChange}
                               checked={Number(state.ship_method) === 2}/>
                        <label htmlFor={'ship-method-3'} className='checkout-left-body-checkbox'>Quý khách nhận hàng tại
                            VIETNHAT</label>
                    </div>
                    <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                        <input type={'radio'} className='checkout-left-input-checkbox' id='ship-method-4'
                               name="ship_method" value={3} onChange={handleChange}
                               checked={Number(state.ship_method) === 3}/>
                        <label htmlFor={'ship-method-4'} className='checkout-left-body-checkbox'>Muốn thỏa thuận với
                            VIETNHAT</label>
                    </div>
                </div>
                <p>Thời gian giao hàng:</p>
                <div style={{display: 'flex', flexDirection: 'row', justifyContent: 'space-between'}}>
                    <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                        <input type={'radio'} className='checkout-left-input-checkbox' id='time-ship-1'
                               name="time_ship" value={0} onChange={handleChange}
                               checked={Number(state.time_ship) === 0}/>
                        <label htmlFor={'time-ship-1'} className='checkout-left-body-checkbox'>Bất kì</label>
                    </div>
                    <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                        <input type={'radio'} className='checkout-left-input-checkbox' id='time-ship-2'
                               name="time_ship" value={1} onChange={handleChange}
                               checked={Number(state.time_ship) === 1}/>
                        <label htmlFor={'time-ship-2'} className='checkout-left-body-checkbox'>Trong giờ hành
                            chính</label>
                    </div>
                    <div className='checkout-left-input-container' style={{justifyContent: 'flex-start'}}>
                        <input type={'radio'} className='checkout-left-input-checkbox' id='time-ship-3'
                               name="time_ship" value={2} onChange={handleChange}
                               checked={Number(state.time_ship) === 2}/>
                        <label htmlFor={'time-ship-3'} className='checkout-left-body-checkbox'>Ngoài giờ hành
                            chính</label>
                    </div>
                </div>
            </div>
            <div className='checkout-right-total-left'>
                {isSending ? null :
                    <LinkTemplate path={'/cart'}>
                        <p className='checkout-right-total-left-more'>Quay về giỏ hàng</p>
                    </LinkTemplate>
                }
                <button className='checkout-left-button' onClick={handleOrder} disabled={isSending ? true : false}>
                        <span>
                            <i className="fas fa-shopping-cart" style={{marginRight: '0.5em'}}></i>
                            {
                                isSending ? "Đang gửi đơn hàng..." : "Đặt hàng"
                            }
                        </span>
                </button>
            </div>
        </section>
    );
};

export default CheckoutLeft;