import React, {useMemo} from 'react';
import './CheckoutRight.css'
import {useDispatch, useSelector} from "react-redux";
import {TableCell} from "../../cart/Cart";
import LinkTemplate from "../../template/LinkTemplate";
import {changeAmountCart, removeFromCard} from "../../../redux/actions/actions";
import Divider from "../../template/Divider";

const CheckoutRight = () => {
    const cartData = useSelector(state => state.cartReducer)
    const total = useMemo(() => {
        let total = cartData.reduce((total, cur) => {
            return total + cur.amount * cur.sale_price
        }, 0)
        return total
    }, [cartData])
    const total_product = useMemo(() => {
        let total = cartData.reduce((total, cur) => {
            return total + cur.amount
        }, 0)
        return total
    }, [cartData])
    return (
        <div className='checkout-right-container-root'>
            <p className='checkout-left-title-info'><i className="fa fa-shopping-cart checkout-left-icon"></i>
                {`Đơn hàng (${total_product} sản phẩm)`}</p>
            <div className='checkout-right-body'>
                <table className='checkout-right-table'>
                    <tr style={{borderBottom: '1px solid black'}}>
                        <TableCell align={'left'} className={'checkout-right-cell cell-header'}>Sản phẩm</TableCell>
                        <TableCell align={'center'} className={'checkout-right-cell cell-header'}>Số lượng</TableCell>
                        <TableCell align={'center'} className={'checkout-right-cell cell-header checkout-right-mobile'}>Đơn giá</TableCell>
                        <TableCell align={'center'} className={'checkout-right-cell cell-header total-price-element'}>Thành
                            tiền</TableCell>
                    </tr>
                    {cartData.map((item, index) => {
                        return <tr key={index}>
                            <TableCell align={'center'} className={'checkout-right-cell'}>
                                <LinkTemplate path={`/product/${item.product_id}`}>
                                    <div style={{display: 'flex'}}>
                                        <img style={{width: '4em', height: '4em'}}
                                             src={item.url_image_main}/>
                                        <div style={{
                                            display: 'flex',
                                            flexDirection: 'column',
                                            justifyContent: 'center'
                                        }}>
                                            <span className='checkout-right-name'>{item.name}</span>
                                        </div>
                                    </div>
                                </LinkTemplate>
                            </TableCell>
                            <TableCell align={'center'} className={'checkout-right-cell'}>
                                {item.amount}
                            </TableCell>
                            <TableCell className={'checkout-right-cell checkout-right-mobile'}
                                       align={'center'}>
                                <div>
                                    <div
                                        style={{fontSize: '1.05em'}}>{`${Number(item.sale_price).toLocaleString('en')}đ`}</div>
                                    <div style={{
                                        fontSize: '1.05em', textDecoration: 'line-through'
                                    }}>{`${Number(item.price).toLocaleString('en')}đ`}</div>
                                </div>
                            </TableCell>
                            <TableCell className={'checkout-right-cell total-price-element'}
                                       align={'center'}>{`${Number(item.amount * item.sale_price).toLocaleString('en')}đ`}</TableCell>
                        </tr>
                    })
                    }

                </table>
            </div>
            <div className='checkout-right-total'>
                <div className='checkout-right-total-right'>
                    <div className='checkout-right-total-right-row'>
                        <span className='checkout-right-total-right-row-title'>Tạm tính:</span>
                        <span
                            className='checkout-right-total-right-row-body'>{`${Number(total).toLocaleString('vi')}đ`}</span>
                    </div>
                    <div className='checkout-right-total-right-row'>
                        <span className='checkout-right-total-right-row-title'>Phí vận chuyển:</span>
                        <span className='checkout-right-total-right-row-body'>{`Liên hệ`}</span>
                    </div>
                    <Divider/>
                    <div className='checkout-right-total-right-row' style={{fontWeight: 'bold', fontSize: '1em'}}>
                        <span className='checkout-right-total-right-row-title'>Tổng tiền:</span>
                        <span className='checkout-right-total-right-row-body'
                              style={{color: 'var(--third-bg-color)'}}>{`${Number(total).toLocaleString('vi')}đ`}</span>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CheckoutRight;