import React, {useEffect, useMemo, useState} from "react";
import './Cart.css'
import {useSelector} from "react-redux";
import clsx from "clsx";
import {useDispatch} from 'react-redux'
import {changeAmountCart, removeFromCard} from "../../redux/actions/actions";
import LinkTemplate from "../template/LinkTemplate";
import SaleItem from "../template/SaleItem";
import RouteTemplate from "../template/RouteTemplate";
import BodyTemplate from "../template/BodyTemplate";
import NoProduct from "../../assets/empty-cart.png"
import MetaTag from "../template/MetaTag/MetaTag";
import {APP_URL, DESC_DEFAULT, LOGO_DEFAULT} from "../../constants/Constants";
export const TableCell = ({className, children, align}) => {
    return <td className={clsx(
        className, 'tc', (align === 'center') && 'tc-center', (align === 'left') && 'tc-left', (align === 'right') && 'tc-right'
    )}>
        {children}
    </td>
}

export default function Cart() {
    const cartData = useSelector(state => state.cartReducer)
    const dispatch = useDispatch()
    const total = useMemo(() => {
        let total = cartData.reduce((total, cur) => {
            return total + cur.amount * cur.sale_price
        }, 0)
        return total
    }, [cartData])
    useEffect(() => {
    }, [])
    return (
        <BodyTemplate>
            <MetaTag title={`Giỏ hàng`} description={DESC_DEFAULT} image_url={LOGO_DEFAULT} url={APP_URL} />
            <RouteTemplate data={
                [
                    {
                        name: "Trang chủ",
                        path: "/"
                    },
                    {
                        name: "Giỏ hàng",
                    }
                ]
            }/>
            <div className='cart-container'>
                <div className='cart-title'>Giỏ hàng</div>
                <hr className='cart-divider'></hr>
                {
                    cartData.length ===0? <div className='cart-image-container'>
                            <img src={NoProduct} className='cart-image'/>
                            <p>Không có sản phẩm nào trong giỏ hàng!</p>
                    </div> :
                    <div>
                        <div className='cart-container-desktop'>
                            <table className='cart-table'>
                                <tr>
                                    <TableCell align={'center'} className='cart-table-head'>Ảnh sản phẩm</TableCell>
                                    <TableCell align={'center'} className='cart-table-head'>Tên sản phẩm</TableCell>
                                    <TableCell align={'center'} className='cart-table-head'>Đơn giá</TableCell>
                                    <TableCell align={'center'} className='cart-table-head'>Số lượng</TableCell>
                                    <TableCell align={'center'} className='cart-table-head'>Thành tiền</TableCell>
                                    <TableCell align={'center'} className='cart-table-head'>Xóa</TableCell>
                                </tr>
                                {
                                    cartData.map((item, index) => {
                                        return <tr key={index}>
                                            <TableCell align={'center'}>
                                                <LinkTemplate path={`/product/${item.product_id}`}>
                                                    <img style={{width: '5em', height: '5em'}}
                                                         src={item.url_image_main}/>
                                                </LinkTemplate>
                                            </TableCell>
                                            <TableCell align={'left'}>{item.name}</TableCell>
                                            <TableCell
                                                align={'center'}>{Number(item.sale_price).toLocaleString('en')}</TableCell>
                                            <TableCell align={'center'}>
                                                <button className='cart-button-add' onClick={() => {
                                                    let newValue = Number(item.amount) + 1
                                                    dispatch(changeAmountCart({
                                                        product_id: item.product_id,
                                                        amount: newValue
                                                    }))
                                                }}>+
                                                </button>
                                                <input className='cart-input-amount' type='number' value={item.amount}
                                                       onChange={(e) => {
                                                           if (Number(e.target.value) <= 0) {
                                                               return
                                                           }
                                                           dispatch(changeAmountCart({
                                                               product_id: item.product_id,
                                                               amount: Number(e.target.value)
                                                           }))
                                                       }}/>
                                                <button className='cart-button-add' onClick={() => {
                                                    let newValue = Number(item.amount) - 1
                                                    if (newValue <= 0) {
                                                        return
                                                    }
                                                    dispatch(changeAmountCart({
                                                        product_id: item.product_id,
                                                        amount: newValue
                                                    }))
                                                }}>-
                                                </button>
                                            </TableCell>
                                            <TableCell
                                                align={'center'}>{Number(item.amount * item.sale_price).toLocaleString('en')}</TableCell>
                                            <TableCell align={'center'}>
                                                <div className='cart-button-delete' onClick={() => {
                                                    dispatch(removeFromCard({
                                                        product_id: item.product_id
                                                    }))
                                                }}>
                                                    <i className="far fa-trash-alt"></i>
                                                </div>
                                            </TableCell>
                                        </tr>
                                    })
                                }
                            </table>
                            <div className={'cart-table-price-container'}>
                                <LinkTemplate path='/'>
                                    <div className='cart-button-more'>Xem thêm mặt hàng</div>
                                </LinkTemplate>
                                <div style={{flexGrow: 1}}></div>
                                <div>
                                    <table>
                                        <tr>
                                            <TableCell align={'left'}>Tạm tính</TableCell>
                                            <TableCell align={'right'}
                                                       className={'cart-price'}>{`${Number(total).toLocaleString('en')}đ`}</TableCell>
                                        </tr>
                                        <tr>
                                            <TableCell align={'left'}>Tổng tiền thanh toán</TableCell>
                                            <TableCell align={'right'}
                                                       className={'cart-price'}>{`${Number(total).toLocaleString('en')}đ`}</TableCell>
                                        </tr>
                                    </table>
                                    <LinkTemplate path='/thanh-toan'>
                                        <div className='cart-button-more btn-checkout'>Thanh toán</div>
                                    </LinkTemplate>
                                </div>
                            </div>
                        </div>
                        <div className='cart-container-mobile'>
                            {
                                cartData.map((item, index) => (
                                    <div key={index} style={{padding: '0.5em'}}>
                                        <div className='cart-element-mobile'>
                                            <SaleItem data={{
                                                product_id: item.product_id,
                                                price: item.price,
                                                sale_price: item.sale_price,
                                                url_image_main: item.url_image_main,
                                                name: item.name
                                            }}/>
                                            <div className='cart-element-right'>
                                                <div>
                                                    <button className='cart-button-add' onClick={() => {
                                                        let newValue = Number(item.amount) + 1
                                                        dispatch(changeAmountCart({
                                                            product_id: item.product_id,
                                                            amount: newValue
                                                        }))
                                                    }}>+
                                                    </button>
                                                    <input className='cart-input-amount' type='number'
                                                           value={item.amount}
                                                           onChange={(e) => {
                                                               if (Number(e.target.value) <= 0) {
                                                                   return
                                                               }
                                                               dispatch(changeAmountCart({
                                                                   product_id: item.product_id,
                                                                   amount: Number(e.target.value)
                                                               }))
                                                           }}/>
                                                    <button className='cart-button-add' onClick={() => {
                                                        let newValue = Number(item.amount) - 1
                                                        if (newValue <= 0) {
                                                            return
                                                        }
                                                        dispatch(changeAmountCart({
                                                            product_id: item.product_id,
                                                            amount: newValue
                                                        }))
                                                    }}>-
                                                    </button>
                                                </div>
                                                <div className='cart-button-delete delete-mobile' onClick={() => {
                                                    dispatch(removeFromCard({
                                                        product_id: item.product_id
                                                    }))
                                                }}>
                                                    <i className="far fa-trash-alt"></i> Xóa
                                                </div>
                                            </div>
                                        </div>
                                        <hr className='cart-divider card-main'></hr>
                                    </div>
                                ))
                            }
                            <div style={{textAlign: 'center'}}>Tổng tiền thanh toán</div>
                            <div style={{textAlign: 'center', marginTop: '0.5em', marginBottom: '0.5em'}}
                                 className={'cart-price'}>{`${Number(total).toLocaleString('en')}đ`}</div>
                            <LinkTemplate path='/thanh-toan'>
                                <div className='cart-button-more btn-checkout'>Thanh toán</div>
                            </LinkTemplate>
                            <LinkTemplate path='/'>
                                <div className='cart-button-more-mobile'>Xem thêm mặt hàng</div>
                            </LinkTemplate>
                        </div>
                    </div>
                }
            </div>
        </BodyTemplate>
    );
}